@use 'global' as *;

.privacypolicy__inner {
    position: relative;
    @include mq("md"){
        max-width: rem(850);
    }
}


.privacypolicy__inner::before {
    content: "";
    position: absolute;
    width: rem(120);
    height: rem(55);
    background-image: url(../images/common/fish-right.svg);
    background-repeat: no-repeat;
    background-size: contain;
    top: -1.2%;
    left: 4%;
    @include mq("md"){
        width: rem(301);
        height: rem(138);
        top: -7%;
        left: calc(50% - min(50vw, 38.75rem));
    }
}

.privacypolicy__title {
    font-size: rem(24);
    font-weight: $medium;
    font-family: $base-font-family;
    color: $green;
    @include mq("md"){
        font-size: rem(32);
    }
}

.privacypolicy__wrapper {
    margin-top: rem(40);
    @include mq("md"){
        margin-top: rem(36);
    }
}

